import { Container, Grid } from "@mui/material";
import Calci from "./Calculator";
import DFS from "./DFS";
import ImgMediaCard from "./Drinks";
import Express from "./ExpressAPI";
import Spring from "./Rest";
import Travel from "./Travel";
import Butler from "./Butler";
import Fade from 'react-reveal/Fade';
import To from "./to-do-list";
import Krishna from "./Krishna";

const Project =()=>{
    return (<>
    <Container>
<Grid
  container
  direction="row"
  justifyContent="space-around"
  alignItems="center"
>

<Fade bottom> 
       <Grid item className="mt-4">
        <ImgMediaCard></ImgMediaCard>
       </Grid>
 </Fade>
 <Fade bottom>        
 <Grid item className="mt-4">
        <DFS></DFS>
        </Grid>
        </Fade>
 <Fade bottom>   
 <Grid item className="mt-4">
        <Calci></Calci>
        </Grid>
        </Fade>
 <Fade bottom>   
 <Grid item className="mt-4">
        <Travel></Travel>
        </Grid>
        </Fade>
 <Fade bottom>   
 <Grid item className="mt-4">
        <Spring></Spring>
        </Grid>
        </Fade>
 <Fade bottom>   
 <Grid item className="mt-4">
        <Express></Express>
        </Grid>
</Fade>
<Fade bottom> 
<Grid item className="mt-4">
       <Butler></Butler>
  </Grid>
</Fade>
<Fade bottom> 
<Grid item className="mt-4">
       <To></To>
  </Grid>
</Fade>
<Fade bottom> 
<Grid item className="mt-4">
       <Krishna></Krishna>
  </Grid>
</Fade>

        </Grid>
        </Container>
    </>);
    
}
export default Project;