import * as React from 'react';
import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';

export default function Express() {
  return (
    <Card sx={{ minHeight: 200,width:325 }}>
     
      <CardContent>
        <Typography gutterBottom variant="h5" component="div" style={{color:"white"}}>
        OPEN JOBS
        </Typography>
        <Typography variant="body2">
           Job Portal : I have created this portal using react ,spring boot and mongo DB, i post the jobs in this portal and the candidates apply for the jobs. 
        </Typography>
      </CardContent>
      <CardActions>
       <a href="https://openjobsport.herokuapp.com/" target="_blank" className="ms-4"><Button variant="contained" className="ms-5">Check it out</Button></a>
      </CardActions>
    </Card>
  );
}