import * as React from 'react';
import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';

export default function To(){
  return (
    <Card sx={{ minHeight: 200,width:325 }}>
     
      <CardContent>
        <Typography gutterBottom variant="h5" component="div" style={{color:"white"}}>
        To-Do List
        </Typography>
        <Typography variant="body2">
        Simple To-do list using React.js , add your items remove items , connected to db using JPA and Spring Boot.

        </Typography>
      </CardContent>
      <CardActions>
       <a href="https://wordaddition.herokuapp.com/" target="_blank" className="ms-4"><Button variant="contained" className="ms-5">Check it out</Button></a>
      </CardActions>
    </Card>
  );
}